@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700,900&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue",
    Helvetica, "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ",
    Meiryo, "ＭＳ Ｐゴシック", Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol";

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto", sans-serif;
}
.my-masonry-grid { /* Not needed if autoprefixing */ /* Not needed if autoprefixing */
  display: flex;
  margin-left: -30px;
  /* gutter size offset */
  width: auto;
}
.my-masonry-grid_column {
  padding-left: 30px;
  /* gutter size */
  background-clip: padding-box;
}
.my-masonry-grid_column > div {
  /* change div to reference your elements you put in <Masonry> */
  margin-bottom: 10px;
}
.images-container {
  justify-content: center;
  margin-top: 18vh;
  padding-left: 5%;
  padding-right: 2%;
}
@media (min-width: 360px) and (max-width: 768px) {
  /* NavBar */
  #navbar {
    display: inline-block;
    width: 100%;
    min-height: 65px;

    background: white;
    flex-wrap: wrap;
    z-index: 9999;
  }
  .nav-box {
    display: inline-flex;
    background: white;
    max-height: 250px;
    padding-right: 4%;
  }
  .search-box-content {
    flex-basis: 100%;
    display: flex;
    /* flex-wrap: wrap; */
  }
  .images-container {
    justify-content: center;
    margin-top: 18vh;
    padding-left: 5%;
    padding-right: 2%;
  }
  .logo-pinterest {
    margin-top: 3px;
    margin-left: 15px;
    border: none;
    background: transparent;
  }
  .search-box input {
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    height: 75%;
    width: 100%;
    background: #efefef;
    border: 0;
    font-weight: 600;
    padding: 0;
    cursor: text;
    min-width: 150px;
  }
  .icon {
    justify-content: center;
    /* padding: 2%; */
    padding: 10px;
    height: 30px;
    margin-right: 2px;
    width: 30px;
    display: flex;
  }
}

/* Optional, different gutter size on mobile */
@media (max-width: 800px) {
  .my-masonry-grid {
    margin-left: -15px; /* gutter size offset */
  }
  .my-masonry-grid_column {
    padding-left: 15px; /* gutter size offset */
  }
  .my-masonry-grid_column > div {
    margin-bottom: 15px; /* space between items */
  }
}

/* NavBar */
#navbar {
  /* display: flex; */
  width: 100%;
  height: 65px;
  background: white;
  flex-wrap: wrap;
  z-index: 9999;
}

.header-content {
  z-index: 9999;
  top: 0;
  position: fixed;
  box-sizing: border-box;
}
.nav-box {
  display: flex;
  background: white;
}

#btn-pinterest img {
  height: 28px;
  width: 28px;
}
.logo-pinterest img:hover {
  background: #efefef;
  border-radius: 50%;
}

.logo-pinterest {
  margin-top: 3px;
  margin-left: 15px;
  border: none;
  background: transparent;
}

.search-box-content {
  flex-basis: 100%;
  display: flex;
}

.button-content {
  /* margin-top: 5px; */
  flex-basis: 25%;
  display: flex;
  align-content: space-between;
}

.border {
  height: 50px;
  width: 2px;
  border-left: 2px solid #efefef;
  box-sizing: border-box;
  margin-left: 16px;
  display: block;
  margin-top: 15px;
}
hr {
  background: #8e8e8e;
  width: 100%;
  border-top: 1px solid #efefef !important;
  display: block;
  z-index: 9999;
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  border-bottom: 0;
  border-left: 0;
  justify-content: flex-end;
}

/* Search box */
.search-box {
  margin-top: 20px;
  flex-basis: 100%;
  display: flex;
}

.btn-search {
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  color: #8e8e8e;
  padding-top: 6px;
  padding-left: 10px;
  margin-bottom: 5px;
  margin-left: 10px;
  display: flex;
  flex-basis: 15%;
  background: #efefef;
  font-weight: bold;
  font-size: 16px;
  border: none;
  height: 75%;
  width: 100%;
  cursor: pointer;
}

.search-box img {
  padding: 8px;
  margin-bottom: 15px;
  justify-content: center;
  margin-bottom: 15px;
  width: 38px;
  height: 38px;
  display: flex;
}

.search-box input {
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  height: 75%;
  width: 100%;
  background: #efefef;
  border: 0;
  font-weight: 600;
  padding: 0;
  cursor: text;
  min-width: 200px;
}

